    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.marketing-form__section .marketing-form__section__label::before {
    content: counter(section) ".";
    counter-increment: section;
    font-family: Shnobel, Helvetica Neue, Arial, sans-serif;
    color: #ffffff;
    font-size: 5vw;
    font-weight: 300;
    margin-right: 0.5em;
    width: 0.8em;
}

.marketing-form__section .marketing-form__section__label::after {
